
export const LOAD_FIELDS_TEMPLATE = (state, payload) => {
	state.fieldsTemplate = payload.data;
};

export const LOAD_LANGUAGES = (state,payload) => {

	state.langs = Object.assign({}, state.langs, payload.data.languages);
};


export const UPDATE_GAMES = (state, payload) => {
	state.games = Object.assign({}, state.games, payload.kwargs);
};
export const UPDATE_WINNERS = (state, payload) => {
	state.topWinners = Object.assign({}, state.topWinners, payload.kwargs);
};
export const UPDATE_FAVORITES = (state, payload) => {
	let favorites = {games: []};
	payload.kwargs.favorites.forEach(function(game){
		favorites.games.push(game.game);
	});
	state.favorites = Object.assign({}, state.favorites, favorites );
};
export const ADD_GAMES = (state, payload) => {
	state.games.games = Object.assign({}, state.games.games, payload.kwargs.games);
	state.games.currentPageIndex++;
};
export const UPDATE_LAST = (state, payload) => {
	state.lastGames = Object.assign({}, state.lastGames, payload.kwargs);
};
export const UPDATE_VENDORS = (state, payload) => {
	state.vendors = Object.assign({}, state.vendors, payload.kwargs);
};
export const TEST_BONUS = (state, payload) => {
	state.testbonus = Object.assign({}, state.testbonus, payload.kwargs);
};
export const UPDATE_PARAMS = (state, payload) => {
	state.params = Object.assign({}, state.params, payload);
};
export const ADD_PLAY = (state, payload) => {
	if(state.isMobile) {
		let mobilePlay = [{}, {}, {}, {}];
		mobilePlay[0] = payload;
		state.play = mobilePlay;
		state.playIndex = 1;
	} else {
		let newPlay = [];
		newPlay[state.addIndex] = payload;
		state.play = Object.assign([], state.play, newPlay);
		if(state.playIndex < 4) {
			state.playIndex++;
		}
	}

};
export const UPDATE_INDEX = (state, payload) => {
	state.addIndex = payload;
};
export const CHANGE_INDEX = (state) => {
	state.playIndex = 0;
};
export const UPDATE_SIZE = (state, payload) => {
	state.isMobile = payload;
	if(state.isMobile) {
		console.log('START CLEAR');
		for(let i = 0; i < state.play.length; i++) {
			if(state.play[i].name !== undefined) {
				let mobilePlay = [{}, {}, {}, {}];
				mobilePlay[0] = state.play[i];
				state.play = mobilePlay;
				state.playIndex = 1;
				console.log('new Game' +state.play[i].name);
				break;
			}
		}
	}
};
export const REMOVE_PLAY = (state, index) => {
	state.play[index] = {};
	state.playIndex--;
};
