import call from '@/services/callService';

export const USER_LOGIN = async ({ commit }, params) => {
	commit('UPDATE_USER', await call.userLogin(params));
	// await dispatch('GET_SESSION_INFO', null, { root: true });
};

export const GET_USER_PROFILE = async ({commit}) => {
  commit('UPDATE_PROFILE', await call.getProfile());
  commit('UPDATE_COUNTRIES', await call.getCountries());
  commit('UPDATE_LANGUAGES', await call.getLanguages());
  commit('UPDATE_CURRENCIES', await call.getCurrencies());
  commit('UPDATE_PHONE_PREFIXES', await call.getPhonePrefixes());
};

export const GET_PROFILE_STATUS = async ({commit}) => {
  commit('UPDATE_PROFILE_STATUS', await call.getProfileStatus());
};

export const UPDATE_USER_PROFILE = async ({commit}) => {
  commit('UPDATE_PROFILE', await call.getProfile());
};

export const GET_GAMING_ACCOUNTS = async ({commit}) => {
  commit('UPDATE_GAMING_ACCOUNTS', await call.getGamingAccounts());
};

export const GET_LIMITS = async ({commit}) => {
  commit('UPDATE_LIMITS', await call.getLimits());
};
export const UPDATE_USER_RECENTLY = async ({commit}, params) => {
  commit('UPDATE_RECENTLY', params)
};