import Vue from 'vue';
import App from './App.vue';
import store from './store/index';
import VueAwesomeSwiper from 'vue-awesome-swiper';
import VueLoading from 'vue-loading-overlay';
import 'vue-multiselect/dist/vue-multiselect.min.css';
import 'vue-loading-overlay/dist/vue-loading.css';
import 'swiper/dist/css/swiper.css';





const VueWamp = require( 'vue-wamp/dist/vue-wamp.cjs');

import Notifications from 'vue-notification';
Vue.use(Notifications);

Vue.config.productionTip = false;
Vue.use(VueAwesomeSwiper, /* { default global options } */);
Vue.use(VueWamp, {
  transports: [{
    'type': 'websocket',
    'url': (process.env.NODE_ENV === 'development') ? 'wss://api-stage.everymatrix.com/v2' : 'wss://flipperflip-api.everymatrix.com/v2'
  }],
  realm: 'www.flipperflip.com',
  max_retries: 3,
  debug: false,
  onopen: function(session, details) {
    console.log('WAMP connected', session, details);
  },
  onclose: function(reason, details) {
    console.log('WAMP closed: ' + reason, details);
  }
});


let vue = new Vue({
  store,
  render: h => h(App),
  provide: {
    stopSkroll (isOpen) {
      const body = document.body;
      if (isOpen) {
        body.classList.add('modal-open')
      } else {
        body.classList.remove('modal-open')
      }
    }
  }
}).$mount('#app');
