export const getUser = state => state.user;
export const getHasToAcceptTC = state => state.user.hasToAcceptTC;
export const getHasToSetUserConsent = state => state.user.hasToSetUserConsent;
export const getProfile = state => state.profile;
export const getCountries = state => state.countries.countries;
export const getCurrentIPCountry = state => state.countries.currentIPCountry;
export const getLanguages = state => state.languages;
export const getCurrencies = state => state.currencies;
export const getPhonePrefixes = state => state.phonePrefixes;
export const getUserCurrency = state => (state.profile.fields) ? state.profile.fields.currency : '';
export let   getFullCountryName = state => short_name => {
  let match_countries = state.countries.countries.filter((country) => country.code === short_name);
  return match_countries.length ? match_countries[0].name : '';
};
export const getProfileBalance = state => state.gamingAccounts;
export const getLimits = state => state.limits;
export const getDepositLimit = state => state.limits.deposit;
export const getWageringLimit = state => state.limits.wagering;
export const getLossLimit = state => state.limits.loss;
export const getSessionLimit = state => state.limits.session;

export const getProfileStatus = (state) => {
  return state.accountConfirmation ? state.accountConfirmation :
         state.user.isProfileIncomplete ? 'edit' : 'verify'; // edit || verify \\ done || waiting_confirm
};
export const getGamingAccountId = (state) => {
 return state.gamingAccounts.accounts
    ? state.gamingAccounts.accounts.filter((account) => account.isBonusAccount).length
     ? state.gamingAccounts.accounts.filter((account) => account.isBonusAccount)[0].id
     : ''
    : '';
};
export const getRecentlyGames = state => state.recently;