export const getPromos = state => state.promo_list;

export const getPromoBannerMain = state => {
  let banners = state.promo_list.filter(promo =>  promo.type === 'main');
  return banners.length ? banners[0] : '';
};

export const getPromoBannerSecond = state => {
  let banners = state.promo_list.filter(promo =>  promo.type === 'second');
  
  return banners.length ? banners[0] : '';
};

export const getPromoBannersDefault = state => {
  let banners = state.promo_list.filter(promo =>  promo.type === 'default');
  
  return banners.length ? banners : '';
};

export const getPromoBannersOffer = state => {
  let banners = state.promo_list.filter(promo =>  promo.type === 'offer');
  
  return banners.length ? banners : '';
};

export let getPromoPage = state => promo_name => {
  return state.promo_pages[promo_name] ?  state.promo_pages[promo_name] : '';
};
