<template>
  <div class="game-block" @click="showBlockMenu" style="
			width: 23%;
			background-color: #306eff;
			color: #fff">
    <div class="game-block__image" :style="{ backgroundImage: 'url(' + image + ')' }">
      <div class="game-block__wrapper" :class="{'show_options' : showGameMenu}">

        <a href="https://flipperflip.com" class="game-block__play" style="display: flex; align-items: center;justify-content: center;" v-if="game.realMode">
          Play Now
        </a>
        <a href="https://flipperflip.com" class="game-block__fun" v-if="game.funMode">
          Fun Play
        </a>
      </div>
    </div>
    <div class="game-block__title">
      {{ game.shortName }}
    </div>
    <div class="game-block__played" style="color: #fff;">
      {{ popularity }}
    </div>

  </div>

</template>

<script>
  import {mapGetters, mapMutations, mapActions} from 'vuex';
  import call from '@/services/callService';
  export default {
    name: "GameBlock",
    props: ['game'],
    data() {
      return {
        isFav: false,
        showGameMenu: false
      }
    },
    computed: {
      ...
        mapGetters({
          favorites: 'getFavorites',
          fparams: 'getFparams',
          addIndex: 'getAddIndex',
        }),
      image() {
        // let images = JSON.parse(this.game.icons);
        // return images[114]['*'];
        return this.game.thumbnail;
      },
      popularity() {
        return this.game.popularity + ' played';
      },
    },
    methods: {
      ...mapMutations([
        'UPDATE_PLAY',
      ]),
      ...mapActions([
        'UPDATE_CASINO_PLAY',
        'UPDATE_CASINO_FAVORITES',
      ]),
      async changeFavorite() {
        let game = {
          "type": "game",
          "id": this.game.slug
        };
        if (this.isFav) {
          this.removeFavorite(game)
            .then((res) => {
              this.UPDATE_CASINO_FAVORITES(this.fparams);
              this.isFav = false;
              console.log(`EM removeFavorite: ${res}`);
              this.$notify({
                group: 'app',
                type: 'success',
                text: `remove game from Favorites`
              });
            }).catch((err) => {
            const message = err.kwargs.desc || JSON.stringify(err);
            this.$notify({
              group: 'app',
              type: 'error',
              text: message
            });
            console.error(`EM removeFavorite Error: ${message}`);
          });
        } else {
          this.addFavorite(game)
            .then((res) => {
              this.UPDATE_CASINO_FAVORITES(this.fparams);
              this.isFav = true;
              console.log(`EM addFavorite: ${res}`);
              this.$notify({
                group: 'app',
                type: 'success',
                text: `Add game to Favorites`
              });
            }).catch((err) => {
            const message = err.kwargs.desc || JSON.stringify(err);
            this.$notify({
              group: 'app',
              type: 'error',
              text: message
            });
            console.error(`EM addFavorite Error: ${message}`);
          });
        }
      },
      async removeFavorite(game) {
        await call.removeFavorite(game);
      },
      async addFavorite(game) {
        await call.addFavorite(game);
      },
      showBlockMenu() {
        this.showGameMenu = !this.showGameMenu;
      }
    },
    async mounted() {
      let vue = this;
      await this.favorites.games.forEach(function (item) {
        if (item.slug === vue.game.slug) {
          vue.isFav = true;
        }
      });
    }
  }
</script>

<style lang="sass" scoped>
  .game-block
    display: inline-block
    padding: 10px
    margin-right: 20px
    margin-bottom: 20px
    width: 100%
    background-color: $colorSecond
    border-radius: 10px
    border: 1px solid rgba(224, 224, 224, 0.5)

    &:hover
      box-shadow: 0 4px 42px rgba(65, 65, 65, 0.12)

    &__image
      width: 100%
      height: rem(155)
      border-radius: 10px
      background-size: cover
      background-repeat: no-repeat
      background-position: center

      &:hover
        .game-block__wrapper
          display: flex


    &__wrapper
      display: none
      position: relative
      background-color: rgba(0, 5, 33, 0.7)
      width: 100%
      height: 100%
      flex-direction: column
      justify-items: center
      align-items: center
      align-content: center
      justify-content: center
      &.show_options
        display: flex

    &__favorite
      display: block
      position: absolute
      right: 8px
      top: 8px
      width: 18px
      height: 18px

      img
        &:nth-child(2)
          display: none

        &.--active
          display: block

        &.--disable
          display: none
        width: 100%
        height: auto

      &:hover
        img:nth-child(1)
          display: none

        img:nth-child(2)
          display: block

    &__play
      width: auto
      min-width: 40%
      text-align: center
      border-radius: 6px
      background-color: rgb(255, 128, 14)
      outline: none
      border: none
      height: rem(28)
      padding: 0 rem(5)
      line-height: rem(24)
      vert-align: middle
      color: $whiteText
      font-family: $mainFont
      font-size: rem(13)
      font-weight: 500
      margin-bottom: 10px

      &:focus, &:hover
        background-color: rgb(255, 110, 14)

    &__fun
      width: auto
      min-width: 40%
      text-align: center
      border-radius: 6px
      background-color: transparent
      border: 1px solid $whiteText
      outline: none
      height: rem(28)
      padding: 0 rem(5)
      line-height: rem(24)
      vert-align: middle
      color: $whiteText
      font-family: $mainFont
      font-size: rem(13)
      font-weight: 500

      &:focus, &:hover
        background-color: rgb(255, 110, 14)
        border: 1px solid rgb(255, 110, 14)

    &__title
      margin: 10px
      font-size: rem(16)
      font-family: $mainFont
      font-weight: 600
      text-align: left

    &__played
      color: $textColorMain
      font-family: $SharpSans
      font-size: rem(12)
      font-weight: 200
      line-height: rem(15)
      text-align: left
      margin-left: 10px

      &:before
        content: ''
        display: inline-block
        width: 10px
        height: 10px
        margin-right: 5px
        background-image: url("../assets/img/svg/main/triangle_blue.svg")
        background-size: cover

</style>
