<script>
    // @ is an alias to /src
		import GamesSlider from '@/components/sliders/GamesSlider.vue';
	  import TreasureSection from "@/components/sections/TreasureSection";
		import BannerSection from "@/components/sections/BannerSection";
		import Menu from '@/components/Menu';
		import Footer from '@/components/Footer';
	  import {mapGetters, mapActions} from 'vuex';
    export default {
        name: "home",
				methods: {
					...mapActions([
						'UPDATE_CASINO_GAMES',
						'UPDATE_CASINO_PARAMS',
						'GET_FIELDS_TEMPLATE'
					])
				},
        components: {
						Footer,
						TreasureSection,
						BannerSection,
						GamesSlider,
						Menu
        },
				computed: {
		      ...mapGetters({
						fieldsTemplate: 'fieldsTemplate',
		        games: 'getGames',
		        params: 'getParams',
		        fields: 'getFields',
		        banners_offer: 'getPromoBannersOffer',
		        banner_main: 'getPromoBannerMain',
		        banner_second: 'getPromoBannerSecond',
		        banners_default: 'getPromoBannersDefault',
		        session: 'getSessionInfo',
		      }),
					currentParams() {
	        return {
	          "filterByName": [],
	          "filterBySlug": [],
	          "filterByVendor": [],
	          'filterByCategory': [],
	          'expectedFields': this.fields.Slug +
	            this.fields.Tags +
	            this.fields.ShortName +
	            this.fields.Name +
	            this.fields.Thumbnail +
	            this.fields.Url +
	            this.fields.FunMode +
	            this.fields.RealMode +
	            this.fields.NewGame +
	            this.fields.Vendor +
	            this.fields.Popularity +
	            this.fields.AnonymousFunMode
	        };
      	}
    	},
			async mounted() {

				await this.GET_FIELDS_TEMPLATE([]);

	      try {
	        this.UPDATE_CASINO_PARAMS(this.currentParams);
	        await this.UPDATE_CASINO_GAMES(this.params);
	      } catch (err) {
	        const message = JSON.stringify(err);
	        this.$notify({
	          group: 'app',
	          type: 'error',
	          text: message
	        });
	        console.error(`EM Get Games Error: ${message}`);
	      }
	    }
    };

		const getOffsetTop = element => {
	  let offsetTop = 0;
	  while(element) {
	    offsetTop += element.offsetTop;
	    element = element.offsetParent;
	  }
	  return offsetTop;
	}
		function checkScrollToCircles() {

			let circle1 = document.getElementsByClassName('circle-1')[0];

			let offset = getOffsetTop(circle1) + 100;
			if(window.pageYOffset + window.innerHeight >= offset) {
				document.getElementsByClassName('circle-1')[0].classList.add('animate-show');
				let startTimer = 400;


				var els = document.getElementsByClassName('circle');
				Array.prototype.forEach.call(els, function(item) {
					if(!item.classList.contains('circle-1')) {
						setTimeout(() => {
							item.classList.add('animate-show');
						}, startTimer);
						startTimer = startTimer + 300;
					}
				});

			}

		}



		window.addEventListener('scroll', checkScrollToCircles, false);




		// bubbles

		function bubbles(el) {
			let $bubbles = el;
			const min_bubble_count = 20;
			const max_bubble_count = 30;
			const min_bubble_size = 7;
			const max_bubble_size = 24;
  		let bubbleCount = min_bubble_count + Math.floor(Math.random() * (max_bubble_count + 1));

		  for (let i = 0; i < bubbleCount; i++) {

				let container = document.createElement('div');

				container.className = 'bubble-container';

				let bubble = document.createElement('div');

				bubble.className = 'bubble';

				container.appendChild(bubble);

		    $bubbles.appendChild(container);
		  }

		  $bubbles.querySelectorAll('.bubble-container').forEach(function(item){
		    let pos_rand = Math.floor(Math.random() * 101);

		    let size_rand = min_bubble_size + Math.floor(Math.random() * (max_bubble_size + 1));

		    let delay_rand = Math.floor(Math.random() * 16);

		    let speed_rand = 10 + Math.floor(Math.random() * 9);

		    let blur_rand = Math.floor(Math.random() * 1);

		    let $this = item;

				$this.style.left = pos_rand + '%';
				$this.style['animation-duration'] = speed_rand + 's';
				$this.style['-moz-animation-duration'] = speed_rand + 's';
				$this.style['-webkit-animation-duration'] = speed_rand + 's';
				$this.style['-ms-animation-duration'] = speed_rand + 's';
				$this.style['animation-delay'] = delay_rand + 's';
				$this.style['-moz-animation-delay'] = delay_rand + 's';
				$this.style['-webkit-animation-delay'] = delay_rand + 's';
				$this.style['-ms-animation-delay'] = delay_rand + 's';
				$this.style['filter'] = 'blur(' + blur_rand  + 'px)';
				$this.style['-webkit-filter'] = 'blur(' + blur_rand  + 'px)';
				$this.style['-moz-filter'] = 'blur(' + blur_rand  + 'px)';
				$this.style['-ms-filter'] = 'blur(' + blur_rand  + 'px)';



				var doc = $this;
				var notes = null;
				for (var i = 0; i < doc.childNodes.length; i++) {
				    if (doc.childNodes[i].className == "bubble") {
				      notes = doc.childNodes[i];
							doc.childNodes[i].style.width = size_rand + 'px';
							doc.childNodes[i].style.height = size_rand + 'px';
				      break;
				    }
				}



		  });
}

window.onload = () => {

	bubbles(document.getElementsByClassName('bubbles-1')[0]);
	bubbles(document.getElementsByClassName('bubbles-2')[0]);

};



</script>
