<template>
  <div class="slider" style="margin-top: 80px">

      <h3 class="slider__header slider__title" style="margin: 20px 0;">
        <a href="https://flipperflip.com/" class="site__subtitle" style="color: #fff;">Top games</a>
      </h3>

    <div class="slider__viewer">

      <div class="slider__slider" style="justify-content: center;">
        <game-block v-for="(game, i) in slides" :key="i" :game="game"></game-block>
      </div>
    </div>
  </div>
</template>

<script>
  import GameBlock from "../GameBlock";
  export default {
    name: "GamesSlider",
    components: {GameBlock},
    props: {
      games: {
        type: Object,
        required: true
      }
    },
    computed: {
      swiperOption() {
        return {
          slidesPerView: 5.5,
          slidesOffsetBefore: -(document.documentElement.clientWidth / 5.5) * 1.75,
          centeredSlides: true,
          spaceBetween: 30,
          autoHeight: true,
          autoWidth: true,
          allowTouchMove: true,
          navigation: {
            nextEl: '.slider__arrow-next',
            prevEl: '.slider__arrow-prev'
          },
          breakpoints: {
            1280: {
              slidesPerView: 4.5,
              spaceBetween: 30,
              slidesOffsetBefore: -(document.documentElement.clientWidth / 4.5) * 1.75
            },
            1024: {
              slidesPerView: 4.5,
              spaceBetween: 30,
              slidesOffsetBefore: -(document.documentElement.clientWidth / 4.5) * 1.75
            },
            768: {
              slidesPerView: 2.5,
              spaceBetween: 30,
              slidesOffsetBefore: 0,
              centeredSlides: false
            },
            640: {
              slidesPerView: 1.5,
              spaceBetween: 30,
              slidesOffsetBefore: 0,
              centeredSlides: false
            },
            320: {
              slidesPerView: 1.5,
              spaceBetween: 30,
              slidesOffsetBefore: 0,
              centeredSlides: false
            }
          }
        }
      },
      slides() {
        return Object.values(this.games);
      }
    }
  }
</script>

<style lang="sass" scoped>
  .slider
    margin: rem(40) 0
    width: 100%

    &__title
      margin-bottom: 0
    &__header
      text-align: left
      a
        color: $textColorMain
        font-size: rem(24)
        line-height: rem(32)
        font-family: $Avenir
        text-align: left
    &__viewer
      width: 100%
    &__arrow-prev, &__arrow-next
      user-select: none
      display: flex
      align-items: center
      justify-content: flex-end
      cursor: pointer
      font-size: rem(24)
      transition: font-size 300ms
    &__arrow-prev
      margin-right: rem(10)
    &__controls
      display: flex
      flex-direction: row
      justify-content: flex-end
  .swiper-button-disabled
    font-size: rem(16)
    opacity: .3
    cursor: default
  /deep/ .swiper-wrapper
    padding: rem(20) 0
  .slider__slider
    display: flex
    flex-wrap: wrap
</style>
