import Vue from 'vue';
import Vuex from 'vuex';
import userModule from './modules/user/index';
import authModule from './modules/authorization/index';
import generalModule from './modules/general/index';
import casinoModule from './modules/casino/index';
import landingsModule from './modules/landings/index';
import createPersistedState from 'vuex-persistedstate';

Vue.use(Vuex);

export default new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  plugins: [createPersistedState({
    paths: ['userModule', 'sessionModule', 'authModule', 'generalModule']
  })],
  modules: {
    userModule,
    casinoModule,
    generalModule,
    landingsModule,
    authModule,
  },
  state: {},
  mutations: {},
  actions: {}
});
