import Vue from 'vue'

export default {
  validateUsername(params) {
    return Vue.Wamp.call('/user/account#validateUsername', null, {
      'userName': params
    });
  },



  validateEmail(params) {
    return Vue.Wamp.call('/user/account#validateEmail', null, {
      'email': params
    });
  },
  getPasswordPolicy() {
    return Vue.Wamp.call('/user/pwd#getPolicy');
  },
  getCurrencies() {
    return Vue.Wamp.call('/user/account#getCurrencies', null, {});
  },
  getCountries() {
    return Vue.Wamp.call('/user/account#getCountries', null, {});
  },
  getSessionInfo() {
    return Vue.Wamp.call('/user#getSessionInfo', null, {});
  },
  accountRegister(params) {
    return Vue.Wamp.call('/user/account#register', null, params);
  },
  accountActivate(id) {
    return Vue.Wamp.call('/user/account#activate', null, {
      "verificationCode": id
    });
  },
  userLogin(params) {
    return Vue.Wamp.call('/user#login', null, {
      "usernameOrEmail": params.usernameOrEmail,
      "password": params.password,
    })
  },
  userLogout() {
    return Vue.Wamp.call('/user#logout', null, {});
  },
  sendResetPwdEmail(params) {
    return Vue.Wamp.call('/user/pwd#sendResetPwdEmail', null, {
      "email": params.email,
      "changePwdURL": params.pwdUrl,
      "captchaPublicKey": params.sitekey,
      "captchaResponse": params.captchaResponse
    })
  },
  isResetPwdKeyAvailable(key) {
    return Vue.Wamp.call('/user/pwd#isResetPwdKeyAvailable', null, {
      "key": key
    })
  },
  resetPassword(params) {
    return Vue.Wamp.call('/user/pwd#reset', null, {
      "key": params.key,
      "password": params.password
    })
  },
  getProfile() {
    return Vue.Wamp.call('/user/account#getProfile');
  },
  getProfileStatus() {
    return Vue.Wamp.call('/user/account#getProfileStatus');
  },
  getPhonePrefixes() {
    return Vue.Wamp.call('/user/account#getPhonePrefixes');
  },
  getLanguages() {
    return Vue.Wamp.call('/user/account#getLanguages');
  },
  accountUpdateProfile(params) {
    return Vue.Wamp.call('/user/account#updateProfile', null, params);
  },
  userDeposit(params) {
    return Vue.Wamp.call('/user/hostedcashier#deposit', null, params);
  },
  userWithdraw(params) {
    return Vue.Wamp.call('/user/hostedcashier#withdraw', null, params);
  },
  getGames(params) {

    return Vue.Wamp.call('/casino#getGames',null, params);
  },
  getGamingAccounts() {
    return Vue.Wamp.call('/user/account#getGamingAccounts', null, {
      "expectBalance": true,
      "expectBonus": true
    })
  },
  changePassword(params) {
    return Vue.Wamp.call('/user/pwd#change', null, params)
  },
  getTransactionHistory(params) {
    return Vue.Wamp.call('/user#getTransactionHistory', null, params);
  }
}
