export const UPDATE_CASINO_GAMES = async ({commit}, params) => {

  if (params.pageIndex > 1) {
    commit('ADD_GAMES', await call.getGames(params));
  } else {


    commit('UPDATE_GAMES', await call.getGames(params));
  }
};
