export const UPDATE_CASINO_PLAY = async ({commit}, params) => {
  if (Number.isInteger(params)) {
    commit('REMOVE_PLAY', params);
  } else {
    if (params.currentMode && params.userMode) {
      let options = {
        "slug": params.slug,
        "tableID": "",
        "realMoney": true
      };
      let realUrl = await call.getRealMode(options);
      params.realUrl = realUrl;
      commit('ADD_PLAY', params);
    } else {
      if (params.userMode) {
        let options = {
          "slug": params.slug,
          "tableID": "",
          "realMoney": true
        };
        let realUrl = await call.getRealMode(options);
        params.realUrl = realUrl;
        commit('ADD_PLAY', params);
      } else {
        commit('ADD_PLAY', params);
      }
    }
  }
};
